export const schema = {
  "title": "Graphlopedia Submission Form",
  "description": "Submitting this form does something maybe",
  type: "object",
  properties: {
    "id": {
      "type": "string",
      "title": "Graph ID Number",
      "pattern": "G[0-9]{6}$"
    },
    "deg_seq": {
      "type": "array",
      "title": "Degree Sequence",
      "items": {
        "type": "number"
      }
    },
    "name": {
      "type": "string",
      "title": "Name of Graph"
    },
    "num_vert": {
      "type": "number",
      "title": "Number of Vertices"
    },
    "edges": {
      "type": "array",
      "title": "Edge List",
      "items": {
        "type": "array",
        "minItems": 2,
        "maxItems": 2,
        "items": {
          "type": "number"
        }
      }
    },
    "images": {
      "type": "array",
      "title": "Upload Images",
      "items": {
        "type": "object",
        "properties": {
          "title": {
            "type": "string",
            "title": "Image Title"
          },
          "description": {
            "type": "string",
            "title": "Description"
          },
          "src": {
            "type": "string",
            "title": "File",
            "format": "data-url"
          }
        },
        "required": [
          "src"
        ],
        "additionalProperties": false
        }
    },
    "links": {
      "type": "array",
      "title": "Links",
      "items": {
        "type": "string"
      }
    },
    "refs": {
      "type": "array",
      "title": "References",
      "items": {
        "type": "string"
      }
    },
    "comments": {
      "type": "array",
      "title": "Comments",
      "items": {
        "type": "string"
      }
    },
    "contrib": {
      "type": "array",
      "title": "Contributors",
      "items": {
    "type": "object",
    "properties": {
      "fi": {
        "type": "string"
      },
      "fname": {
        "type": "string"
      },
      "lname": {
        "type": "string"
      },
      "website": {
        "type": "string",
        "format": "uri"
      }
    },
    "required": [
      "lname"
    ],
    "additionalProperties": false
  },
    }
  },
};


